// Learn cc.Class:
//  - https://docs.cocos.com/creator/manual/en/scripting/class.html
// Learn Attribute:
//  - https://docs.cocos.com/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        //开始游戏按钮
        kaishi:{
            default:null,
            type:cc.Node,
        },
        //体力存在的文本
        jingli:{
            default:null,
            type:cc.Label,
        },
        //金币系统的0/10储存量
        jinbixitong:{
            default:null,
            type:cc.Label,
        },
        //金币的等级，最高五级
        level:{
            default:null,
            type:cc.Label,
        },
        //体力从满到有空隙进行倒计时时间文本
        shijian:{
            default:null,
            type:cc.Label,
        },
        //金币
        jinqian:{
            default:null,
            type:cc.Label,
        },
        //更多关卡
        xuanguan:{
            default:null,
            type:cc.Node,
        },
        //备用体力
        beiyong:{
            default:null,
            type:cc.Node,
        },
        //加号用钱买东西
        jiahao:{
            default:null,
            type:cc.Node,
        },
        //金币系统等级升级按钮
        shengjit:{
            default:null,
            type:cc.Node,
        },
        //金币系统取钱按钮
        quqian:{
            default:null,
            type:cc.Node,
        },
        //体力上限增加按钮
        shangxian:{
            default:null,
            type:cc.Node,
        },
        //提示没钱或体力满了文本
        manji:{
            default:null,
            type:cc.Label,
        },
        //获取体力购买的预制体
        cardFab:{
            default:null,
            type:cc.Prefab,
        },
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        //常驻节点
        this.dateJs=cc.find("mainData").getComponent("Maingong");
        // console.log(this.dateJs.date["qian"]);
        // this.dateJs.date["beiyongzui"]=this.dateJs.getTime();
        //预制体生成后，不能在继续生成
        this.chupeng=0;
        cc.director.preloadScene('MainGame', function () {
            
        });
        cc.director.preloadScene('MainGuan', function () {
            
        });
        //用来给体力倒计时做的
        this.jishi=60;
        //金钱不够显示文本
        this.manjizhuangtai=0;
        //金钱不够显示文本的状态为了做特效的效果
        this.manjizhuangtait=0;
        this.jinbigoule=0;
        this.jinbigoulet=0;

    },

    start () {
        //进入游戏game
        this.kaishi.on(cc.Node.EventType.TOUCH_START,
            this.touchDown, this);
        //更多关卡
        this.xuanguan.on(cc.Node.EventType.TOUCH_START,
            this.touchDown1, this);
        //金币系统取钱
        this.quqian.on(cc.Node.EventType.TOUCH_START,
            this.onBut1Down, this);
        //金币系统升级
        this.shengjit.on(cc.Node.EventType.TOUCH_START,
            this.onBut2Down, this);
        //体力上限按钮
        this.shangxian.on(cc.Node.EventType.TOUCH_START,
                this.touchDown20, this);
        //金币购买购买体力
        this.jiahao.on(cc.Node.EventType.TOUCH_START,
            this.touchDown2, this);
        //备用体力
        this.beiyong.on(cc.Node.EventType.TOUCH_START,
                this.touchDown12, this);
        // this.dateJs.date["tilitingliu"]=this.dateJs.getTime();
    },
    //进入游戏game界面的单击事件
    touchDown(event){
        // console.log(123);
        //如果体力大于0，进入游戏体力减一，反之体力不够，出来文本。
         if(this.dateJs.date["tili"]>0){
            cc.director.loadScene("MainGame");
            this.dateJs.date["tili"]-=1;
            this.dateJs.save();
         }
         else{
            this.manji.string="体力不够了，请购买";
            this.manjizhuangtai=1;
            this.manjizhuangtait=0;
         }
         //记住体力满了后第一次下降体力的开始时间
         if(this.dateJs.date["tili"]==(this.dateJs.date["tilishangxian"]-1)){
            this.dateJs.date["begin"]=this.dateJs.getTime();
        }
         
    // console.log(touchLoc.x,touchLoc.y);

   },
   //金币购买体力一点加号，生成买体力按钮
   touchDown2(event){
    if(this.chupeng==0){
        this.diyi =cc.instantiate(this.cardFab);
        this.node.addChild(this.diyi);
        this.diyi.y=50;
        this.chupeng=1;
        this.sheng=this.diyi.getComponent("Menuti");
    
        this.sheng.goumai.on(cc.Node.EventType.TOUCH_START,
            this.touchDown10, this);
            this.sheng.cuo.on(cc.Node.EventType.TOUCH_START,
                this.touchDown11, this);
    }
    
},
//加号购买体力的购买体力预制体的购买
touchDown10(event){
    if(this.dateJs.date["tili"]<this.dateJs.date["tilishangxian"]){
        if(this.dateJs.date["qian"]>=200){
            this.dateJs.date["qian"]-=200;
            this.dateJs.date["tili"]+=1;
            this.diyi.destroy();
            this.chupeng=0;
            this.dateJs.save();
        }
        else{  
            this.sheng.jinbibugou.string="没钱了"    
            this.sheng.jinbibugou.node.opacity=255;
            cc.tween(this.sheng.jinbibugou.node)
            .to(1,{opacity:0})
           .start()
        }
        
      
    }
    else{
        this.sheng.jinbibugou.string="体力满了"    
            this.sheng.jinbibugou.node.opacity=255;
            cc.tween(this.sheng.jinbibugou.node)
            .to(1,{opacity:0})
           .start()
    }
    
},
//金币购买预制体的差
touchDown11(event){
    this.diyi.destroy();
    this.chupeng=0;

},
//体力上限按钮
touchDown20(event){
    // console.log(this.dateJs.date["tilishangxian"])
    //钱大于500，体力上限加10，最多20；
    if(this.dateJs.date["qian"]>=500){
        if(this.dateJs.date["tilishangxian"]<20){
            this.dateJs.date["tilishangxian"]+=10;
            this.dateJs.date["qian"]-=500;
            this.dateJs.save();
            if(this.dateJs.date["tili"]==10){
                this.dateJs.date["begin"]=this.dateJs.getTime();
                this.dateJs.save();
    
    
            }
        }
        else{
            this.manji.string="体力上限满级";
            this.manjizhuangtai=1;
            this.manjizhuangtait=0;
        }
        
    }
    else{
        this.manji.string="金钱不够";
            this.manjizhuangtai=1;
            this.manjizhuangtait=0;
    }

},

//备用体力
touchDown12(event){
    // this.diyi.destroy();
    if(this.chupeng==0){
        this.diyi1 =cc.instantiate(this.cardFab);
        this.node.addChild(this.diyi1);
        this.diyi1.y=50;
        this.sheng=this.diyi1.getComponent("Menuti");
        this.sheng.title.string="备用体力";
        this.sheng.jiangli.string="领取";
        this.sheng.ziliao.string="最多领取三个体力呦";
        this.chupeng=1;
        // console.log(this.dateJs.date["beiyong"]);
        this.sheng.num.string=""+this.dateJs.date["beiyong"];
        this.sheng.goumai.on(cc.Node.EventType.TOUCH_START,
            this.touchDown13, this);
            this.sheng.cuo.on(cc.Node.EventType.TOUCH_START,
                this.touchDown14, this);
    }
   

},
//备用体力的购买
touchDown13(event){
    // this.diyi1.destroy();
    if(this.dateJs.date["beiyong"]>0){
        if(this.dateJs.date["tili"]<this.dateJs.date["tilishangxian"]){
            if((this.dateJs.date["tili"]+this.dateJs.date["beiyong"])<this.dateJs.date["tilishangxian"]){
            this.dateJs.date["tili"]+=this.dateJs.date["beiyong"]
            this.dateJs.date["beiyong"]=0;
            this.dateJs.date["beiyongzui"]=this.dateJs.getTime();
            }
            
           else {
                var tong=this.dateJs.date["tili"]+this.dateJs.date["beiyong"];
                // console.log(147147);
                this.shijian.node.opacity=0;
                this.dateJs.date["tili"]=this.dateJs.date["tilishangxian"];
                // console.log(tong-this.dateJs.date["tilishangxian"]);
                this.dateJs.date["beiyong"]=tong-this.dateJs.date["tilishangxian"];
                // console.log(this.dateJs.date["beiyong"]);

                this.dateJs.save();
                
                // console.log(this.dateJs.date["beiyong"]);
                
            }
            this.chupeng=0;
            this.dateJs.save();
            this.diyi1.destroy();
       }
    }
    else{
            this.sheng.jinbibugou.string="备用体力被领完了"    
            this.sheng.jinbibugou.node.opacity=255;
            cc.tween(this.sheng.jinbibugou.node)
            .to(1,{opacity:0})
           .start()
    }
    
},
//备用体力的差
touchDown14(event){
    this.chupeng=0;
    this.diyi1.destroy();
},
//选关页面
   touchDown1(event){
    // console.log(123);
      cc.director.loadScene("MainGuan");
   
// console.log(touchLoc.x,touchLoc.y);

},
    update (dt) {
        // console.log(this.dateJs.date["beiyong"]);
        //体力的时间
        var timeLenght = 1000 ;  //  加一单位的时长
        this.jingli.string=""+this.dateJs.date["tili"]
        this.jinqian.string="金钱:"+this.dateJs.date["qian"]
        if(this.dateJs.date["tili"]<this.dateJs.date["tilishangxian"]){
            // console.log(1213);
            var now = this.dateJs.getTime() ;
            if(now - this.dateJs.date["begin"] >= timeLenght){
                var num = Math.floor((now - this.dateJs.date["begin"])/timeLenght);
                this.shijian.node.opacity=255;

                // if(num<=60){
                //     this.jishi=60-num;
                // }
                // else if(num>60){
                //     this.jishi=60-(num%60);
                     
                // }
                // this.jishi-=num;
                // this.dateJs.date["ceshi"]=this.jishi
                // console.log(this.dateJs.date["ceshi"]);
                if(num>=60){
                    // console.log(num);
                    this.dateJs.date["tili"]+=Math.floor(num/60) ;
                    this.dateJs.date["begin"]+=(timeLenght*Math.floor(num/60)*60);
                    if(this.dateJs.date["tili"]>=this.dateJs.date["tilishangxian"]){
                        this.dateJs.date["tili"]=this.dateJs.date["tilishangxian"];
                        this.shijian.node.opacity=0;
                        
                        // this.dateJs.date["begin"]=this.dateJs.getTime();

                    }

                //  
                   // this
                }
                var num = Math.floor((now - this.dateJs.date["begin"])/timeLenght);

                //  if(num<=60){
                    this.jishi=60-num;
                    this.dateJs.date["taiyang"]=this.jishi;
                    this.dateJs.save();
                // }
                // else if(num>60){
                //     this.jishi=60-(num%60);
                     
                // }
                this.shijian.string=""+this.jishi;
            }

        }
        else if(this.dateJs.date["tili"]>=this.dateJs.date["tilishangxian"]){
            this.shijian.string=""+0;

        }
        //备用体力
        // console.log(this.dateJs.date["beiyongzui"]);
        if(this.dateJs.date["beiyong"]<3){
            // console.log(12142)
            var now1 = this.dateJs.getTime();
            if(now1 - this.dateJs.date["beiyongzui"] > 60000){
                var num1 = Math.floor((now1 - this.dateJs.date["beiyongzui"])/60000);
                // console.log(num1);
                if(num1>=1){
                    this.dateJs.date["beiyong"]+=Math.floor(num1) ;
                    this.dateJs.date["beiyongzui"]+=(60000*num1);
                    if(this.dateJs.date["beiyong"]>=3){
                        // console.log(1454545);
                        this.dateJs.date["beiyong"]=3;
                        // this.dateJs.date["begin"]=this.dateJs.date["beiyongzui"]

                    }
                    this.dateJs.save();

                //     // this
                }
            }
        } 
        else{
            this.dateJs.date["beiyongzui"]=this.dateJs.getTime();

        }
        //金币系统
        var now2 = this.dateJs.getTime() ;
        // console.log(this.dateJs.date["homeQian"])
        // console.log(this.dateJs.getMax())
        if(this.dateJs.date["homeQian"] < this.dateJs.getMax())
        {
            if(now2 - this.dateJs.date["begin1"] > 60000)//如果没有催款
            {
            // console.log(now2 - this.dateJs.date["begin1"] )
                
                var num3 = Math.floor((now2 - this.dateJs.date["begin1"])/60000) ;
                var qian = num3 * this.dateJs.date["level"] ;
                if(this.dateJs.date["homeQian"] + qian >= this.dateJs.getMax())
                {
                    this.dateJs.date["homeQian"] = this.dateJs.getMax() ;
                }
                else
                {
                    this.dateJs.date["homeQian"] += qian ;
                    this.dateJs.date["begin1"] += num3 * 60000 ;
                }
                this.dateJs.save() ;
                this.jinbixitong.string = this.dateJs.date["homeQian"]+"/"+this.dateJs.getMax() ;
            }
        }
        else{
            this.dateJs.date["begin1"] = this.dateJs.getTime() ;
        }
        // console.log(this.dateJs.date["qian"])
        // this.jinqian.string="金钱"+this.dateJs.date["qian"]
        this.jinbixitong.string=""+this.dateJs.date["homeQian"]+"/"+this.dateJs.getMax();
        this.level.string=""+this.dateJs.date["level"]+"级";
        //文本消失显示
        if(this.manjizhuangtai==1){
            switch(this.manjizhuangtait){
                case 0:
                   this.manji.node.opacity=255;
                   this.manjizhuangtait=1;
                   break;
                case 1:
                    this.manji.node.opacity-=4;
                    if(this.manji.node.opacity<=0){
                        this.manji.node.opacity=0;
                        this.manjizhuangtai=0;
                        this.manjizhuangtait=0;
                    }
            }
        }
    },
    onBut1Down()
    {
        //  取钱系统，存下的钱大于0，
        if(this.dateJs.date["homeQian"] > 0)
        {
            this.dateJs.date["qian"] += this.dateJs.date["homeQian"] ;
            this.dateJs.date["homeQian"] = 0 ;
            this.dateJs.date["begin1"] = this.dateJs.getTime() ;
            this.jinqian.string = "金钱:"+this.dateJs.date["qian"] ;
            this.level.string = ""+this.dateJs.date["level"]+"级" ;
            this.jinbixitong.string = this.dateJs.date["homeQian"]+"/"+this.dateJs.getMax() ;
            this.dateJs.save() ;

        }
    },
    onBut2Down()
    {
        //  升级等级小于5，钱大于100，等级升级钱减少，
        if(this.dateJs.date["level"]<5){
            if(this.dateJs.date["qian"] >= 100)
            {
                this.dateJs.date["level"] ++ ;
                this.dateJs.date["qian"] -= 100 ;
                
                this.jinqian.string = ""+this.dateJs.date["qian"] ;
                this.level.string = ""+this.dateJs.date["level"]+"级" ;
                this.jinbixitong.string = this.dateJs.date["homeQian"]+"/"+this.dateJs.getMax() ;
                this.dateJs.save() ;
            }
            else{
                this.manji.string="金币不够";
            this.manjizhuangtai=1;
            this.manjizhuangtait=0;
            }
        }
        else{
            this.manji.string="金币已满级";
            this.manjizhuangtai=1;
            this.manjizhuangtait=0;
            // this.manji.opacity=255;
        }
       
    },
});
