cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
         jishi: {
            default: null,
            type: cc.Label
        },
        cardFab:{
            default:null,
            type:cc.Prefab,
        },
        },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.shijian=30;
        this.shijian1=30;
    },

    start () {
        // 倒计时
        this.schedule(this.doCountdownTime,1);
        this.schedule(this.doCountdownTime1,0.1);
        

    },
    resetGame(){
         this.shijian=30;
    },
    doCountdownTime(){
        //每秒更新显示信息
        if(this.game_js.jieshu==0){
            if (this.shijian > 0 ) {
                this.shijian -= 1;
                this.game_js.timedao.scaleX=this.game_js.timedao.scaleX-1/30;
                this.jishi.string=""+this.shijian;
                this.countDownShow(this.shijian);
            }
        }
       
    },
    doCountdownTime1(){
        //每秒更新显示信息
            if (this.shijian1 > 0 ) {
                this.shijian1 -=0.1;
                this.countDownShow1(this.shijian1);
            }
       
    },
    countDownShow1(temp){
    
        if(temp <= 0){
            //倒计时结束
            this.unschedule(this.doCountdownTime1);
            //执行其他操作
        }
    },
    countDownShow(temp){
    
        if(temp <= 0){
            //倒计时结束
            this.unschedule(this.doCountdownTime);
            //执行其他操作
            this.game_js.jieshu=1;
            this.game_js.heise.opacity=100;
            this.game_js.shibai();
            // this.game_js.shibai.opacity=255;
        }
    },
    update (dt) {
        // this.shijian1=Math.floor(this.shijian1 * 10) / 10;
        // console.log(this.shijian1);
    },
});
