// Learn cc.Class:
//  - https://docs.cocos.com/creator/manual/en/scripting/class.html
// Learn Attribute:
//  - https://docs.cocos.com/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        cardFab:{
            default:null,
            type:cc.Prefab,
        },
        cardFab1:{
            default:null,
            type:cc.Prefab,
        },
        //返回主界面
        fanhui:{
            default:null,
            type:cc.Node,
        },
        //购买体力
        goumai:{
            default:null,
            type:cc.Node,
        },
        //体力文本
        tili:{
            default:null,
            type:cc.Label,
        },
        tilifu:{
            default:null,
            type:cc.Label,
        },
        //消失文本
        jinbimeile100:{
            default:null,
            type:cc.Label,
        },
        
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.dateJs=cc.find("mainData").getComponent("Maingong");
        this.guan=[]; 
        this.suoda=18;
        this.score=0;
        this.chupeng=0;
        this.jinbigoule=0;
        this.jinbigoulet=0;
        this.guankai=0;
        this.cardJS=[];
        for(var i=0;i<18;i++){
            this.guan[i]=cc.instantiate(this.cardFab);
            this.node.addChild(this.guan[i])
            this.guan[i].x=-200+135*(i%4);
            this.guan[i].y=110-150*Math.floor(i/4);
            this.score++;
            // this.guanshu.string=""+this.score;
            this.cardJS[i]=this.guan[i].getComponent("GuanS");
            this.cardJS[i].init(i);
            this.cardJS[i].num.string=""+this.score;

        }
        for(var j=0;j<this.dateJs.date["guan1"];j++){
           this.cardJS[j].num.node.opacity=255;
           this.cardJS[j].zuijia.node.opacity=255;
           if(this.dateJs.date["zuikuaitime"][j]==30){
            this.cardJS[j].zuijia.string="没有最佳"
           }
           else{
              this.cardJS[j].zuijia.string="最佳:"+this.dateJs.date["zuikuaitime"][j]+"秒";
              
           }
        //    this.cardJS[j].zuijia.string=""+this.this.dateJs.date["zuikuaitime"][j];
           this.cardJS[j].suo.opacity=0;
           this.guan[j].on(cc.Node.EventType.TOUCH_START,
                this.touchDown, this);
        }

    },
    touchDown(event){
        if(this.chupeng==0){
            if(this.dateJs.date["tili"]>0){
                
                var js=event.target.getComponent('GuanS');
                // console.log(js.id);
                this.guankai=js.id+1;
                this.dateJs.date["guanqia"]=this.guankai;
                this.dateJs.save();
                // console.log(this.guankai);
                cc.director.loadScene("MainGame");
                // cc.director.loadScene("MainGame");
                this.dateJs.date["tili"]-=1;
                if(this.dateJs.date["tili"]==(this.dateJs.date["tilishangxian"]-1)){
                    // console.log(147852);
                    this.dateJs.date["begin"]=this.dateJs.getTime();
                }
                this.dateJs.save();
             }
            else{
                // console.log(1212);
                this.jinbigoule=1;
                this.jinbigoulet=0;
    
                // this.jinbimeile100.node.opacity=255;
            }
        }

   },
    start () {
        this.fanhui.on(cc.Node.EventType.TOUCH_START,
            this.touchDown2, this);
        this.goumai.on(cc.Node.EventType.TOUCH_START,
            this.touchDown1, this);
    },
    touchDown11(event){
        this.diyi.destroy();
        this.chupeng=0;
    
    },
    touchDown10(event){
        if(this.dateJs.date["tili"]<this.dateJs.date["tilishangxian"]){
            if(this.dateJs.date["qian"]>200){
                this.dateJs.date["qian"]-=200;
                this.dateJs.date["tili"]+=1;
                this.chupeng=0;
                this.diyi.destroy();
                this.dateJs.save();
            }
            else{
                this.sheng.jinbibugou.string="没钱了"    
                this.sheng.jinbibugou.node.opacity=255;
                cc.tween(this.sheng.jinbibugou.node)
                .to(1,{opacity:0})
               .start()
            }
            
          
        }
        else{
            this.sheng.jinbibugou.string="体力满了"    
                this.sheng.jinbibugou.node.opacity=255;
                cc.tween(this.sheng.jinbibugou.node)
                .to(1,{opacity:0})
               .start()
        }
        
    },
    touchDown2(event){
        cc.director.loadScene("MainMenu");
    },
    touchDown1(event){
        // console.log(1212);
        if(this.chupeng==0){
            this.diyi =cc.instantiate(this.cardFab1);
            this.node.addChild(this.diyi);
            this.diyi.y=50;
            this.chupeng=1;
            this.sheng=this.diyi.getComponent("Menuti");
            if(this.chupeng==1){
                this.sheng.goumai.on(cc.Node.EventType.TOUCH_START,
                    this.touchDown10, this);
                    this.sheng.cuo.on(cc.Node.EventType.TOUCH_START,
                        this.touchDown11, this);
            }
        }
        
        
    },
    update (dt) {
        // console.log(this.dateJs.date["zuikuaitime"][17]);
        this.tili.string=""+this.dateJs.date["tili"];
        if(this.jinbigoule==1){
            switch(this.jinbigoulet){
                case 0:
                   this.jinbimeile100.node.opacity=255;
                   this.jinbigoulet=1;
                   break;
                case 1:
                    this.jinbimeile100.node.opacity-=4;
                    if(this.jinbimeile100.node.opacity<=0){
                        this.jinbimeile100.node.opacity=0;
                        this.jinbigoule=0;
                        this.jinbigoulet=0;
                    }
            }
        }

    },
});
